<template>
    <!-- 补料单 -->
    <div class="heigth-div percent-table">
        <el-table
            :data="sendData"
            :height="tabHeight"
            border
            highlight-current-row
            class="adjust-table table-line-hight">
            <el-table-column prop="sendnum" label="送货单号" align="center" width="105"></el-table-column>
            <el-table-column prop="goodsname" label="产品名称" align="center" width="75"></el-table-column>
            <el-table-column prop="outdate" label="出站时间" align="center" ></el-table-column>
            <el-table-column label="送货方量/出站方量" align="center" width="180">
                <template slot-scope="scope">
                    <p class="main-text">{{scope.row.sendqty}}</p>
                    <p class="secondary-text">{{scope.row.outqty}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="error" label="误差" align="center" width="60"></el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                sendData:[
                    {sendnum:'S00097743',goodsname:'C10',outdate:this.recentDate(1)+' 14:15',sendqty:16,outqty:14,error:(16/14).toFixed(2)+'%'},
                    {sendnum:'S00097745',goodsname:'C10',outdate:this.recentDate(1)+' 15:45',sendqty:12,outqty:10,error:(12/10).toFixed(2)+'%'},
                    {sendnum:'S00097746',goodsname:'C20',outdate:this.recentDate(1)+' 16:15',sendqty:16,outqty:15,error:(16/15).toFixed(2)+'%'},
                    {sendnum:'S00097747',goodsname:'C20',outdate:this.recentDate(1)+' 17:35',sendqty:13,outqty:12,error:(13/12).toFixed(2)+'%'},
                    {sendnum:'S00097748',goodsname:'C30',outdate:this.recentDate(1)+' 10:15',sendqty:16,outqty:14.5,error:(16/14.5).toFixed(2)+'%'},
                    {sendnum:'S00097749',goodsname:'C35',outdate:this.recentDate(1)+' 12:15',sendqty:12,outqty:11,error:(12/11).toFixed(2)+'%'},
                ],
                tabHeight:330
            }
        },

        mounted() {
            this.init();
        },

        methods: {
            init(){
                this.getlist();
            },

            getlist(){

            },
        },
    }    
</script>
<style scoped lang="less" type="text/less">
    .heigth-div{
        height: 95%;
    }
</style>


